.taroify-cascader__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: var(--cascader-header-height, 96px);
  padding: var(--cascader-header-padding, 0 var(--padding-md, 32px));
  font-size: var(--cascader-header-font-size, var(--font-size-lg, 32px));
  font-weight: var(--cascader-header-font-weight, var(--font-weight-bold, 500));
  line-height: var(--cascader-header-line-height, 40px);
}
.taroify-cascader__tabs .taroify-tabs__tab {
  flex: none;
  padding: 0 20px;
}
.taroify-cascader__tabs.taroify-tabs--line .taroify-tabs__wrap {
  height: var(--cascader-tabs-height, 96px);
}
.taroify-cascader__tabs .taroify-tabs__nav--complete {
  padding-right: 12px;
  padding-left: 12px;
}
.taroify-cascader__tab {
  font-weight: var(--cascader-tab-font-weight, var(--font-weight-bold, 500));
  color: var(--cascader-tab-color, var(--text-color, var(--gray-8, #323233)));
}
.taroify-cascader__tab--inactive {
  font-weight: normal;
  color: var(--cascader-inactive-tab-color, var(--gray-6, #969799));
}
.taroify-cascader__options {
  box-sizing: border-box;
  height: var(--cascader-options-height, 768px);
  padding-top: 12px;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.taroify-cascader__option {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--cascader-option-padding, 20px var(--padding-md, 32px));
  font-size: var(--cascader-option-font-size, var(--font-size-md, 28px));
  line-height: var(--cascader-option-line-height, var(--line-height-md, 40px));
  cursor: pointer;
}
.taroify-cascader__option:active, .taroify-cascader__option--hover {
  background: var(--cascader-option-active-background-color, var(--active-color, var(--gray-2, #f2f3f5)));
}
.taroify-cascader__option--active {
  font-weight: var(--cascader-active-option-font-weight, var(--font-weight-bold, 500));
  color: var(--cascader-active-option-color, var(--cascader-active-color, var(--primary-color, #1989fa)));
}
.taroify-cascader__option--disabled {
  color: var(--cascader-disabled-option-color, var(--gray-5, #c8c9cc));
  cursor: not-allowed;
}
.taroify-cascader__option--disabled:active {
  background-color: transparent;
}
.taroify-cascader__active-icon {
  font-size: var(--cascader-active-icon-font-size, 36px);
}